const heroSlider = document.querySelector('.hero__swiper');


let myHeroSwiper = new Swiper(heroSlider, {
	slidesPerView: 1,
	spaceBetween: 5,
	navigation: {
		nextEl: '.swiper-button-next',
		prevEl: '.swiper-button-prev',
	  },
	  loop: true,
	  slideToClickedSlide: true,
	  centeredSlides: true,
});